body{
  font-family: 'Open Sans', sans-serif !important;
  /* background: #E5DCCD; */
}
.text-slanted{
  font-family: 'Courgette', cursive;
}
a{
  color: inherit;
  text-decoration: none;
  opacity: 0.6;
  transition: opacity 0.4s ease;
}
a:hover{
  text-decoration: none;
  color: inherit;
  opacity: 1;
}

nav{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 3.5rem;
  background: #151515;
  z-index: 10;
  color: #eee;
}
.nav-bar{
  height: 3.5rem;
}
.navbar-links{

}
nav a{
  color: inherit;
  margin-right: 2rem;
}
nav svg{
  fill: #fff;

}
.navlogo{
  opacity: 0.6;
}
header{
  min-height: calc(100vh);
  background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('/olivegrove/images/hero.jpg')center/cover fixed no-repeat;
  position: relative;
  /* padding: 5rem 0; */
}
.header-details{
  height: 100vh;
  color: #fff;
}
.logo-main{
  max-width: 20rem;
  opacity: 0.8;
  margin-bottom: 3rem;
}


.c-red{
  color: rgb(185, 20, 20);
}

.menu-icon-round{
  width: 1.7rem;
  height: 1.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

#meals{
  background:linear-gradient(rgba(255,255,255,0.4),rgba(255,255,255,0.4) ), url('/olivegrove/images/bg1.jpg')center/cover fixed no-repeat;
  padding:5rem 0;
}
#dessert{
  background:linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6) ), url('/olivegrove/images/bg2.jpg')center/cover fixed no-repeat;
  padding:5rem 0;
  color: #bbb;
}
#smoothie{
  background:linear-gradient(rgba(255,255,255,0.4),rgba(255,255,255,0.4) ), url('/olivegrove/images/bg3.jpg')center/cover fixed no-repeat;
  padding:5rem 0;
}
#drink{
  background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5) ), url('/olivegrove/images/bg6.jpg')center/cover fixed no-repeat;
  padding:5rem 0;
  color: #bbb;
}

#meals .menu-item, #smoothie .menu-item{
  border: 1px solid rgba(36, 36, 36, 0.5);
  border-radius: 0.3rem;
  height: 100%;
  color: #232323;
  padding: 1rem;
}
#dessert .menu-item, #drink .menu-item{
  border: 1px solid rgba(170, 170, 170, 0.5);
  border-radius: 0.3rem;
  height: 100%;
  color: #bbb;
  padding: 1rem;
}
#meals .menu-icon-round, #smoothie .menu-icon-round{
  border: 1px solid #232323;
  color: #232323;
}
#dessert .menu-icon-round, #drink .menu-icon-round{
  border: 1px solid #bbb;
  color: #bbb;
}
.icon-round{
  border: 1px solid #383737;
  width: 1.7rem;
  height: 1.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: #504f4f;
}
.icon-delete{
  color: #e04226;
}
.cart-item{
  border: 1px solid rgb(112, 111, 111);
}
.icon-btn{
  border: none;
  background: transparent;
  cursor: pointer;
}
.order-window{
  padding-top: 4rem;
  padding-bottom: 2rem;
  min-height: calc(100vh - 118px);
  background:linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5) ), url('/olivegrove/images/bg5.jpg')center/cover fixed no-repeat;
}
p{
  margin: 0;
}
.anv{
  padding-left: 1.4rem;
  padding-right: 1.4rem;
}

.btn-order-confirm{
  border: none;
  padding: 1rem;
  font-size: 1.2rem;
  color: #eee;
  background: #7E165B;
  border-radius: 1rem;
}
.complete-window{
  
  height: calc(100vh - 118px);
    background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('/olivegrove/images/bg7.jpg')center/cover fixed no-repeat;
    position: relative;
    /* padding: 5rem 0; */
  
}
.complete-message{
  height: calc(100vh - 118px);
  color: rgba(204, 204, 204,0.6);
}
.bon-appetit{
  font-family: 'Sacramento', cursive;
  font-size: 4rem;
  margin-top:2rem;
}
.details-window{
  
  height: calc(100vh - 126px);
    background:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url('/olivegrove/images/bg5.jpg')center/cover fixed no-repeat;
  
  padding-top: 8rem;
}
.btn-pnk{
  color: #eee;
  background: #7E165B;
}
.details-input{
  background: rgba(81, 53, 41, 0.6);
  border: none;
  color: #eee;
}
.details-input:focus{
  background: rgba(81, 53, 41, 0.6);
  border: none;
  color: #eee;
}
footer{
  padding: 1em 0;
  color: #7E7B81;
  background-image: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url('/olivegrove/images/footer.jpg');
  background-repeat:repeat;
}
.heart{
  color: #7E165B;
}
