.social-icon{
  margin-right: 1rem;
  width: 2.3rem;
  height: 2.3rem;
  border: 1px solid #7E7B81;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-footer{
  max-width: 6rem;
  opacity: 0.5;
}
